import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';

@Component({
  tag: 'bh-nav-item',
  styleUrl: 'bh-nav-item.css',
  shadow: true,
})
export class BhNavItem {
  /**
   * Material Symbols icon name
   */
  @Prop() icon: string = 'home';

  /**
   * Label text displayed below the icon
   */
  @Prop() label: string = 'Home';

  /**
   * Whether the nav item is active/selected
   */
  @Prop() isActive: boolean = false;

  /**
   * Whether the nav item is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Event emitted when the nav item is clicked
   */
  @Event() bhClick!: EventEmitter<void>;

  private handleClick = () => {
    if (!this.disabled) {
      this.bhClick.emit();
    }
  };

  private handleKeyDown = (event: KeyboardEvent) => {
    if ((event.key === 'Enter' || event.key === ' ') && !this.disabled) {
      event.preventDefault();
      this.bhClick.emit();
    }
  };

  render() {
    const navItemClasses = {
      'nav-item': true,
      'active': this.isActive,
      'disabled': this.disabled,
    };

    return (
      <div class={navItemClasses}>
        <button
          class="nav-item-button"
          aria-label={this.label}
          type="button"
          onClick={this.handleClick}
          onKeyDown={this.handleKeyDown}
          disabled={this.disabled}
          tabIndex={this.disabled ? -1 : 0}
        >
          <span class="material-symbols-outlined icon">{this.icon}</span>
        </button>
        <p class="nav-item-label">{this.label}</p>
      </div>
    );
  }
}
